<template>
  <div>
    <div class="context" @scroll="context_add" id="timeline" style="overflow-y:scroll; verflow-x:hidden; " :style="'width:'+getAutoWidth(0) +'; height:'+getAutoHeight(0)">
      <div class="timeline-wrapper">
        <div class="timeline">
          <div class="timeline-records" v-for="(record, i) in updataRecords">
            <div class="side-info"><span class="day">{{ record.version_update_time.split(' ')[0] }}</span></div>
            <div class="timeline-icon" :class="iconBg[0]">
              v{{ record.version_number }}
            </div>
            <div class="timeline-content">
              <h2 class="time">{{record.version_update_time.split(' ')[1]}}</h2>
              <div class="details">
                <p v-for="(item, index) in record.version_content">{{ index + 1 + '. ' + item}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'versionUpdate',
    data() {
      return {
        iconBg: [ // 图标背景颜色
          'iconbg-black'
        ],
        updataRecords: [],
        page: 1,
        searchFormData: {
          pageNum: 1,
          pageSize: 14,
          pageCount: 1,
          order: '-version_update_time'
        },
      }

    },
    async mounted() {
      this.updataRecords = await this.search();
    },
    methods: {
      async search() {
        let data = await this.$search(this.searchFormData, 'OAMP_search_sys_update_info_list');
        return data.data
      },
      async context_add() {
        let wrap_height = $('.context').height();  //容器的高度
        let scroll_top = $('.context').scrollTop();   //滚动条的scrolltop
        let scroll_height = $('.context').prop('scrollHeight'); //内容的高度
        let is_height = scroll_height - wrap_height - scroll_top  //判断是否为0，既是否到了底部
        if (is_height === 0) {
          this.searchFormData.pageNum++;
          let addArr = await this.search()
          if (addArr.length != 0) {// 判断查询数据是否为空
            this.updataRecords = this.updataRecords.concat(addArr)
          }

        }
      }
    }
  }
</script>

<style>
  #timeline {
    width: 100%;
    /*height: 100%;*/
    position: absolute;
  }

  .timeline-wrapper * {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }

  .timeline-wrapper {
    width: 90%;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 400;
    position: absolute;
    top: 2em;
    left: 3.6%;
  }

  .timeline {
    padding: 15px 0;
    position: relative;
  }

  .timeline-records {
    margin: 0;
    border: 0 none;
    padding: 0 45px 4px 0;
    width: 50%;
  }

  .side-info {
    left: 50%;
    margin: 34px 0 0 38px;
    position: absolute;
    font-weight: 375;
    color: white;
  }

  .timeline-icon {
    background: #fff;
    line-height: 52px;
    position: absolute;
    text-align: center;
    margin: 16px 0 0 -26px;
    border-radius: 30px;
    color: #FFF;
    font-size: 14px;
    height: 52px;
    width: 52px;
    left: 50%;
    z-index: 10;
  }

  .iconbg-black {
    background: #2F3949;
    box-shadow: 0 0 0 4px white, inset 0 2px 0 rgba(0, 0, 0, 0.3), 0 3px 0 4px rgba(0, 0, 0, 0.3);
  }

  .timeline-content {
    background: rgba(0, 0, 0, 0.2);
    color: white;
    position: relative;
    transition: all 0.4s;
    border-radius: 10px;
    padding: 22px;
    box-shadow: 0px -3px 3px rgba(0, 0, 0, 0.3) inset;
  }

  .time {
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    margin: 0 0 8px;
    color: black;
    font-weight: 375;
  }

  .day {
    font-size: 15px;
  }

  .details {
    margin: 0;
  }

  .details p {
    margin: 0;
    line-height: 18px;
    font-size: 14px;
    font-weight: 300;
  }

  .timeline-content:after {
    content: "";
    display: block;
    border: 15px solid transparent;
    border-left-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    right: -30px;
    top: 26px;
  }

  .timeline-records:nth-child(even) {
    margin-left: 50%;
    padding: 0 0 4px 45px;
  }

  .timeline-records:nth-child(even) .side-info {
    left: auto;
    margin: 18px 38px 0 0;
    text-align: right;
    right: 50%;
  }

  .timeline-records:nth-child(even) .timeline-content:before {
    border: 15px solid transparent;
    border-right-color: rgba(0, 0, 0, 0.3);
    left: -30px;
    right: auto;
  }

  .timeline-records:nth-child(even) .timeline-content:after {
    border: 14px solid transparent;
    border-right-color: rgba(0, 0, 0, 0.3);
    left: -27px;
    right: auto;
  }

  .timeline:before {
    content: "";
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 2px;
    margin-left: -2px;
    position: absolute;
    display: block;
    height: 100%;
    width: 4px;
    left: 50%;
    top: 0;
  }
</style>



